    src: url('../../fonts/Chloe/Regular/Chloe-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../../fonts/Chloe/Regular/Chloe-Regular.woff') format('woff'), /* Modern Browsers */
         url('../../fonts/Chloe/Regular/Chloe-Regular.ttf')  format('truetype'), /* Safari, Android, iOS */
         url('../../fonts/Chloe/Regular/Chloe-Regular.svg#svgChloe-Regular') format('svg'); /* Legacy iOS */
}
